.boom {
	-webkit-animation: fadeOutFromNone 1.1s ease-out;
	-moz-animation: fadeOutFromNone 1.1s ease-out;
	-o-animation: fadeOutFromNone 1.1s ease-out;
	animation: fadeOutFromNone 1.1s ease-out;
}

@-webkit-keyframes fadeOutFromNone {
	0% {
		display: block;
		opacity: 1;
	}

	50% {
		display: block;
		opacity: 0.5;
	}

	100% {
		display: none;
		opacity: 0;
	}
}

@-moz-keyframes fadeOutFromNone {
	0% {
		display: block;
		opacity: 1;
	}

	50% {
		display: block;
		opacity: 0.5;
	}

	100% {
		display: none;
		opacity: 0;
	}
}

