.app {
    outline: 0px solid transparent;
    width: 100vw;
    height: 100vh;
    background: black;
}
  
.switch-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}
    
.switch-wrapper > div {
    position: absolute;
    width: 100%;
}

.custom-ratio-content {
    position: absolute !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.press-btn:hover {
    transform: scale(1.1);
    /* transform-origin: center center; */
}

/* .press-btn:active {
    transform: scale(0.9);
} */

.mdc-text-field__input:focus {
    outline: 1px solid white !important;
}

.bmi-kb-btn {
    font-size: 3vmin; 
    color: black; 
    position: absolute;
    background-color: white;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.bmi-kb-btn:active {
    color: lightgray;
}

.bmi-kb-inner-text {
    left: 50%; 
    top: 50%; 
    transform: translate(-50%, -50%); 
    position: absolute;
}

.page-root-div {
    background: black;
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
}

@keyframes image-slide {
    0% { transform: translate(50%, 80%) scale(1); }
    100% { transform: translate(0px, -50%) scale(1); }  
}

.slide-up-img {
    animation: image-slide 2s cubic-bezier(.5,.5,0,1);
    animation-fill-mode: forwards;
}
